import React from 'react';
import {observer} from 'mobx-react';
import {Link, Redirect, Route, withRouter} from "react-router-dom";
import {Avatar, Button, Icon, message, Modal} from "antd";
import WorkPass from "./WorkPass";
import IndexPalace from "../Palace/Index";
import {baseUrl} from "../../config";
import Zmage from "react-zmage";
import moment from 'moment'
import axios from "axios";
import store from "../About/Index/TaskAcepted/TaskAceptedCell/store";
import {UploadWorkDone} from "../About/Index/TaskAcepted/TaskAceptedCell/UploadWorkDone";
import './index.less'
import imgURL from './tpl-1.png';

@observer
class IndexWork extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visibleCommunications: false,
            contentModal: '',
        };
        const id = this.props.match.params.id;
        this.props.store.fetchWorks(id);
    }

    working = (id) => {
        console.log(id);
        axios.get(
            baseUrl + '/api/enroll',
            {
                params: {
                    token: localStorage.customer_token,
                    id_work: id,
                }
            })
            .then((Response) => {
                const d = Response.data;
                console.log('返回数据', Response.data);
                this.setState({
                    id_senting_work: Response.data.id,
                    // idActive:id,
                });
                if (d.sorry !== undefined || d.error !== undefined) {
                    message.error(d.sorry || d.error);
                } else {
                    // 刷新缓存
                    localStorage.removeItem('my_works');
                    message.success('已成功报名！');
                    // this.forceUpdate();
                    document.location.reload()
                    // setTimeout(() => {
                    //     document.location = `#/about/index/TaskAcepted`
                    // }, 1000)
                }
            })
    };
    renderCommunications = () => {
        const p = this.props.store.communication;
        return (
            <div>
                <h2 className='phone'>手机号:{p.phone}</h2>
                <img src={`${baseUrl}/${p.wx_png}`} alt=''
                     className='avatar'
                     width='100%'
                />
                <h5>提示：长按识别二维码添加微信</h5>
            </div>
        )
    };
    showCommunications = () => {
        this.setState({
            visibleCommunications: true,
            contentModal: this.renderCommunications(),
        });
    };
    showUploadWork = (id) => {
        console.log(id);
        const p = this.props.store.workInfo;
        this.setState({
            visibleCommunications: true,
            contentModal: <UploadWorkDone id_work={p.id}/>,
        });
    };
    renderWorkInfo = () => {
        const p = this.props.store.workInfo;
        const is_enroll = this.props.store.workEnroll;
        const f = this.props.store.workeFile[this.props.match.params.id];
        return (
            p ?
                <div>
                <div className='main-body-work'>
                    <div className='quote-left'>
                    </div>
                    <div className='work-title'>
                        <svg t="1566483491245" className="icon" viewBox="0 0 1024 1024" version="1.1"
                             xmlns="http://www.w3.org/2000/svg" p-id="1665" width="30" height="30">
                            <path
                                d="M470.9888 261.77536l0 98.54976c0 13.3632-10.83392 24.19712-24.19712 24.19712-47.68768 0-73.6256 48.90624-77.21984 145.43872l77.21984 0c13.3632 0 24.19712 10.84416 24.19712 24.19712l0 208.0768c0 13.3632-10.83392 24.19712-24.19712 24.19712L240.90624 786.432c-13.37344 0-24.19712-10.84416-24.19712-24.19712l0-208.0768c0-46.27456 4.6592-88.73984 13.84448-126.22848 9.4208-38.44096 23.87968-72.04864 42.96704-99.90144 19.64032-28.6208 44.20608-51.07712 73.02144-66.72384 29.00992-15.73888 62.74048-23.72608 100.25984-23.72608C460.15488 237.57824 470.9888 248.41216 470.9888 261.77536zM783.09376 384.52224c13.3632 0 24.19712-10.84416 24.19712-24.19712l0-98.54976c0-13.3632-10.83392-24.19712-24.19712-24.19712-37.50912 0-71.23968 7.9872-100.2496 23.72608-28.81536 15.64672-53.39136 38.10304-73.03168 66.72384-19.08736 27.8528-33.54624 61.46048-42.96704 99.91168-9.17504 37.49888-13.83424 79.96416-13.83424 126.21824l0 208.0768c0 13.3632 10.83392 24.19712 24.19712 24.19712l205.8752 0c13.3632 0 24.19712-10.84416 24.19712-24.19712l0-208.0768c0-13.3632-10.83392-24.19712-24.19712-24.19712L706.9696 529.96096C710.51264 433.42848 736.07168 384.52224 783.09376 384.52224z"
                                p-id="1666" fill="#80c1e4"/>
                        </svg>
                        <h2>{p.work_name}</h2>
                    </div>

                    <div>
                        需要：{p.work_person_number}人

                    </div>
                    <div className='work-money'>
                        <h1>
                            {p.work_money !== 0 ? p.work_money : '可以协商/'}{p.work_tpl === 1 || p.work_tpl === 4 ? '元' : '积分'}
                        </h1>
                        <div className='work-times'>
                            <span>
                                发布时间:{moment.unix(p.created_at).format("MM-DD HH:mm")}
                            </span>
                            <br/>
                            <span>
                                截止时间:{
                                moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                    '今天内'
                                    : moment.unix(p.stop_at).format("MM-DD HH:mm")
                            }
                            </span>
                        </div>
                    </div>
                    {/*流程图*/}
                    {p.work_tpl === 1 ? <img src={imgURL} width='100%' alt=''/> : null}
                    <hr/>
                    <div className='more-info'>
                        <div className='quote-right'>
                            <svg t="1566483450803" className="icon" viewBox="0 0 1024 1024" version="1.1"
                                 xmlns="http://www.w3.org/2000/svg" p-id="1492" width="50" height="50">
                                <path
                                    d="M553.0112 762.23488l0-98.54976c0-13.3632 10.84416-24.19712 24.19712-24.19712 47.68768 0 73.6256-48.896 77.21984-145.42848l-77.21984 0c-13.3632 0-24.19712-10.84416-24.19712-24.19712l0-208.0768c0-13.3632 10.84416-24.19712 24.19712-24.19712l205.88544 0c13.3632 0 24.19712 10.84416 24.19712 24.19712l0 208.0768c0 46.27456-4.66944 88.7296-13.83424 126.22848-9.4208 38.44096-23.87968 72.04864-42.97728 99.90144-19.63008 28.6208-44.20608 51.07712-73.0112 66.72384-29.02016 15.73888-62.75072 23.72608-100.27008 23.72608C563.84512 786.432 553.0112 775.58784 553.0112 762.23488zM240.90624 639.47776c-13.3632 0-24.19712 10.84416-24.19712 24.19712l0 98.54976c0 13.3632 10.83392 24.19712 24.19712 24.19712 37.49888 0 71.24992-7.9872 100.2496-23.72608 28.81536-15.64672 53.39136-38.0928 73.02144-66.72384 19.0976-27.8528 33.55648-61.46048 42.97728-99.92192 9.17504-37.49888 13.83424-79.95392 13.83424-126.208l0-208.0768c0-13.3632-10.84416-24.19712-24.19712-24.19712L240.90624 237.568c-13.3632 0-24.19712 10.84416-24.19712 24.19712l0 208.0768c0 13.3632 10.83392 24.19712 24.19712 24.19712l76.1344 0C313.48736 590.58176 287.91808 639.47776 240.90624 639.47776z"
                                    p-id="1493" fill="#85c9ed"/>
                            </svg>
                        </div>
                        {p.work_content
                            ?
                            <div>
                                <div className='blue-bold-font'>
                                    任务补充说明：
                                </div>
                                <div>
                                    {p.work_content}
                                </div>
                            </div>
                            : null
                        }
                        {p.work_url
                            ?
                            <div>
                                <div className='blue-bold-font'>
                                    任务链接：
                                </div>
                                <a href={`http://${p.work_url}`}>
                                    <div className='work-url'>
                                        点击跳转任务相关链接
                                    </div>
                                </a>
                            </div>
                            : null
                        }
                        {
                            f ? <div>
                                    <div className='blue-bold-font'>
                                        任务图片：
                                    </div>
                                    <Zmage src={`${baseUrl}/${f[0].content}`} alt=''
                                           className='avatar'
                                           width='30%'
                                    />
                                    <p>{}</p>
                                </div>
                                : null
                        }
                    </div>
                    <div className='work-cell-row'>
                        {/*头像*/}
                        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                className='avatar'>头像</Avatar>
                        {/*名字*/}
                        <span className='name'>{p.senter_name}</span>
                    </div>
                </div>
                <div className='work-submit-footer'>
                    <div
                        // className={p.stop_at < moment().unix() ? 'submit-footer-cell failed' : 'submit-footer-cell'}
                        className='submit-footer-cell'
                    >

                        <div className='share'>
                            <Icon type="share-alt"/>
                            <br/>
                            分享
                        </div>
                        <div className='action'>
                            {
                                this.props.store.isSenter
                                    //发布者空
                                    ? null
                                    :
                                    p.stop_at < moment().unix()
                                        //过期
                                        ? <div className='failed'>已过期</div>
                                        :
                                        is_enroll
                                            //是否报名
                                            ? <div className='action-right'>
                                                <div
                                                    className='communications'
                                                    onClick={this.showCommunications}
                                                >
                                                    <Icon type="wechat"/>
                                                    <br/>
                                                    <span className='font-small'>联系发布者</span>
                                                </div>
                                                <Button
                                                    size='large'
                                                    className='upload-work'
                                                    onClick={this.showUploadWork}>
                                                    已完成任务，上传凭证
                                                </Button>
                                            </div>
                                            : <Button
                                                size='large'
                                                className='working'
                                                onClick={this.working.bind(this, p.id)}>
                                                立即报名
                                            </Button>


                            }

                        </div>
                    </div>
                </div>
                </div>
                : null
        )
    };
    goBack = () => {
        if (this.props.history.length < 2) {
            document.location = '/#/palace'
        } else {
            this.props.history.goBack()
        }
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };

    render() {
        return (
            <div className="index">
                <div className='back-to-last'>
                    <Link onClick={this.goBack}>
                        <Icon type="left"/>
                        <span>返回</span>
                    </Link>
                </div>
                <div>
                    {/*{*/}
                    {/*    this.props.store.isSenter*/}
                    {/*        ?'隐藏任务'*/}
                    {/*        : null*/}
                    {/*}*/}
                    {/*任务信息*/}
                    {this.renderWorkInfo()}
                    {/*联系/上传凭证*/}
                    <Modal
                        visible={this.state.visibleCommunications}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        style={{textAlign: 'center'}}
                    >
                        {this.state.contentModal}
                    </Modal>
                </div>
                {/*子路由导航*/}
                {
                    this.props.store.isSenter
                        ?
                        <div className='owner'>
                            <div className='nav-route'>
                                <Link
                                    to={`${this.props.match.url}/WillPass`}
                                    className={document.location.hash.includes('WillPass') ? 'active' : 'unactive'}
                                >
                                    待审核
                                </Link>
                                <Link
                                    to={`${this.props.match.url}/Passed`}
                                    className={document.location.hash.includes('Passed') ? 'active' : 'unactive'}
                                >
                                    已通过
                                </Link>
                                {/*<Link*/}
                                {/*    to={`${this.props.match.url}/NotSub`}*/}
                                {/*    className={document.location.hash.includes('NotSub') ? 'active' : 'unactive'}*/}
                                {/*>*/}
                                {/*    未提交*/}
                                {/*</Link>*/}
                                {/*<Link*/}
                                {/*    to={`${this.props.match.url}/Moneyed`}*/}
                                {/*    className={document.location.hash.includes('Moneyed') ? 'active' : 'unactive'}*/}
                                {/*>*/}
                                {/*    已结算*/}
                                {/*</Link>*/}
                                <Link
                                    to={`${this.props.match.url}/NotPass`}
                                    className={document.location.hash.includes('NotPass') ? 'active' : 'unactive'}
                                >
                                    未通过
                                </Link>
                            </div>
                            <div className='routes-container'>

                                {/*<Route exact path={this.props.match.url} render={() => (*/}
                                {/*    <Redirect to={`${this.props.match.url}/WillPass`}/>*/}
                                {/*)}/>*/}
                                <Redirect exact from={this.props.match.url} to={`${this.props.match.url}/WillPass`}
                                          component={IndexPalace}/>
                                <Route exact path={`${this.props.match.url}/WillPass`}
                                       render={(props) => <WorkPass {...props} status={1}
                                                                    id_work={this.props.match.params.id}/>}
                                />
                                <Route exact path={`${this.props.match.url}/Passed`}
                                       render={(props) => <WorkPass {...props} status={0}
                                                                    id_work={this.props.match.params.id}/>}
                                />
                                <Route exact path={`${this.props.match.url}/NotPass`}
                                       render={(props) => <WorkPass {...props} status={-1}
                                                                    id_work={this.props.match.params.id}/>}
                                />
                            </div>
                        </div>
                        : null
                }

            </div>
        );
    }
}


export default withRouter(IndexWork);