import React from 'react'
import './index.less'
import {Icon, message, notification} from 'antd';
import axios from "axios";
import {baseUrl} from "../../config";

const staticUrl = baseUrl;

export default class Pay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // 防止重复点击
            isClick: true,
            // baseUrl:baseUrl,
        };
        console.log(staticUrl);
    }
    handleSubmit = () => {
        const isClick = this.state.isClick;
        console.log('点了！！！！');
        if (isClick) {
            // 获取所有的值
            let allVals = this.props.form.getFieldsValue();
            console.log(allVals);
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    this.setState({isClick: false});
                    console.log('表单值', values);
                    // 发送
                    axios.get(
                        staticUrl + '/api/before_pay',
                        {
                            params: {
                                'total_fee': values.total_fee,
                                'token': localStorage.customer_token,//*
                            }
                        })
                        .then((Response) => {
                            // alert(JSON.stringify(Response.data));
                            const d = Response.data;
                            if (d.success) {
                                const s = d.success;
                                console.log('paying');
                                if (typeof WeixinJSBridge == "undefined"){
                                    if( document.addEventListener ){
                                        document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                                    }else if (document.attachEvent){
                                        document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                                        document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                                    }
                                }else{
                                    onBridgeReady();
                                }
                                function onBridgeReady(){
                                    let WeixinJSBridge = window.WeixinJSBridge;
                                    WeixinJSBridge.invoke(
                                        'getBrandWCPayRequest', {
                                            "appId":s.appId,     //公众号名称，由商户传入
                                            // "timeStamp":JSON.stringify(Date.parse(new Date())/1000),         //时间戳，自1970年以来的秒数
                                            "timeStamp":s.timeStamp,         //时间戳，自1970年以来的秒数
                                            "nonceStr":s.nonceStr, //随机串
                                            "package":s.package,
                                            "signType":'MD5',         //微信签名方式：
                                            "paySign":s.paySign,
                                        },
                                        function(res){
                                            if(res.err_msg == "get_brand_wcpay_request:ok" ){
                                                // 使用以上方式判断前端返回,微信团队郑重提示：
                                                //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                                                axios.get(
                                                    staticUrl + '/api/find',
                                                    {
                                                        params: {
                                                            'token': localStorage.customer_token,//*
                                                            'out_trade_no': s.out_trade_no,
                                                        }
                                                    })
                                                    .then((r) => {
                                                        alert(JSON.stringify(r.data));
                                                        notification.open({
                                                            message: '成功',
                                                            placement: 'bottomLeft',
                                                            icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                                                        });
                                                        document.location = `#/about`
                                                    })

                                            }else {

                                                alert(`
                                                    参数：${
                                                        JSON.stringify(
                                                        {
                                                            "appId":s.appId,     //公众号名称，由商户传入
                                                            "timeStamp":s.timeStamp,         //时间戳，自1970年以来的秒数
                                                            "nonceStr":s.nonceStr, //随机串
                                                            "package":s.package,
                                                            "signType":'MD5',         //微信签名方式：
                                                            "paySign":s.paySign,
                                                        })
                                                        }
                                                    错误原因:
                                                    ${JSON.stringify(res)}
                                                `)
                                            }
                                        });
                                }


                            } else {
                                message.error('充值失败，请稍后重试', d.sorry || d.error);
                                this.setState({isClick: true});
                            }
                        })
                }
            });
        }

    };
    payStatic = (f)=>{
        axios.get(
            staticUrl + '/api/before_pay',
            {
                params: {
                    'total_fee': f,
                    'token': localStorage.customer_token,//*
                }
            })
            .then((Response) => {
                // alert(JSON.stringify(Response.data));
                const d = Response.data;
                if (d.success) {
                    const s = d.success;
                    console.log('paying');
                    if (typeof WeixinJSBridge == "undefined") {
                        if (document.addEventListener) {
                            document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                        } else if (document.attachEvent) {
                            document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                            document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                        }
                    } else {
                        onBridgeReady();
                    }

                    function onBridgeReady() {
                        let WeixinJSBridge = window.WeixinJSBridge;
                        WeixinJSBridge.invoke(
                            'getBrandWCPayRequest', {
                                "appId": s.appId,     //公众号名称，由商户传入
                                // "timeStamp":JSON.stringify(Date.parse(new Date())/1000),         //时间戳，自1970年以来的秒数
                                "timeStamp": s.timeStamp,         //时间戳，自1970年以来的秒数
                                "nonceStr": s.nonceStr, //随机串
                                "package": s.package,
                                "signType": 'MD5',         //微信签名方式：
                                "paySign": s.paySign,
                            },
                            function (res) {
                                if (res.err_msg == "get_brand_wcpay_request:ok") {
                                    // 使用以上方式判断前端返回,微信团队郑重提示：
                                    //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                                    axios.get(
                                        staticUrl + '/api/find',
                                        {
                                            params: {
                                                'token': localStorage.customer_token,//*
                                                'out_trade_no': s.out_trade_no,
                                            }
                                        })
                                        .then((r) => {
                                            console.log(JSON.stringify(r.data));
                                            notification.open({
                                                message: '成功',
                                                placement: 'bottomLeft',
                                                icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                                            });
                                            document.location = `#/about`
                                        })

                                } else {
                                    alert(`
                                        参数：${
                                        JSON.stringify(
                                            {
                                                "appId": s.appId,     //公众号名称，由商户传入
                                                "timeStamp": s.timeStamp,         //时间戳，自1970年以来的秒数
                                                "nonceStr": s.nonceStr, //随机串
                                                "package": s.package,
                                                "signType": 'MD5',         //微信签名方式：
                                                "paySign": s.paySign,
                                            })
                                        }
                                                错误原因:
                                                ${JSON.stringify(res)}
                                                `)
                                }
                            });

                    }
                }
            })
    };
    render() {
        return (
            <div>
                <h1>充值</h1>
                <span>点击要充值的数目</span>
                <div className='pay-container'>
                    <div
                        className='pay-cell'
                        onClick={this.payStatic.bind(this, 100)}>
                        <h3>90积分</h3>
                        <h3>1¥</h3>
                    </div>
                    <div
                        className='pay-cell'
                        onClick={this.payStatic.bind(this, 300)}>
                        <h3>280积分</h3>
                        <h3>3¥</h3>
                    </div>
                    <div
                        className='pay-cell'
                        onClick={this.payStatic.bind(this, 500)}>
                        <h3>480积分</h3>
                        <h3>5¥</h3>
                    </div>
                    <div
                        className='pay-cell'
                        onClick={this.payStatic.bind(this, 1000)}>
                        <h3>980积分</h3>
                        <h3>10¥</h3>
                    </div>
                    <div
                        className='pay-cell'
                        onClick={this.payStatic.bind(this, 3000)}>
                        <h3>2880积分</h3>
                        <h3>30¥</h3>
                    </div>
                    <div
                        className='pay-cell'
                        onClick={this.payStatic.bind(this, 5000)}>
                        <h3>4880积分</h3>
                        <h3>50¥</h3>
                    </div>
                </div>
            </div>
        )
    }
}