const productHost = 'https://www.newbrandser.com';
const devHost = 'https://dev.newbrandser.com';
const testHost = 'https://www.almondlee.cn';

const getHost = function () {
    if (document.location.host === "www.newbrandser.com") {
        return productHost
    }
    if (document.location.host === "dev.newbrandser.com") {
        return devHost
    }
    return testHost
};
export const baseUrl = getHost();
