import React from 'react'
import './index.less'
import {baseUrl} from "../../../../../config";
import store from './store'
import {observer} from "mobx-react";
import WorkCell from "../../../../../cpns/WorkListCell";

@observer
class TaskAceptedCell extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            contentModal: '',
            visibleCommunications: false,
            worksIng: [],
            worksExpired: [],
        };
        // store state初始化
        store.fetchWorks();
        // 设置筛选类型
        store.setTPL(this.props.tpl);
        // 设置排序类型
        store.setSort(this.props.sort);
    }
    renderMyWorks = ()=>{
        const ws = store.worksByTpl;
        const cpns = [];
        for(const key in ws){
            const p = ws[key];
            cpns.push(
                <WorkCell workInfo={p}/>
            )
        }
        return cpns
    };
    getCommunications = (id) => {
        const ws = store.worksByTpl;
        for (const w in  ws) {
            const p = ws[w];
            // console.log(p);
            if (p.id === id) {
                return (
                    <div>
                        <h2 className='phone'>手机号:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5>提示：长按识别二维码添加微信</h5>
                    </div>
                )
            }
        }
    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            contentModal: this.getCommunications(id),
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 4:
                return "兼职类";
            case 0:
                return "其他";
            default:
                return "全部";
        }
    };
    render() {
        return (
                <div className='my-works-accepted'>
                    {this.renderMyWorks()}
                    {/*联系跟上传*/}
                </div>
        )

    }
}
export default TaskAceptedCell;